import React from 'react';
import './Header.css'; // Importe o CSS para o Header

const Header = ({ userProfile, onLogout }) => {
  if (!userProfile) {
    return null; // Não renderiza o Header se userProfile for null
  }

  const { name, profilePic } = userProfile;

  return (
    <header className="header">
      <div className="header-left">
        <img src={profilePic} alt="Foto de Perfil" className="profile-pic" />
        <span>{name}</span>
      </div>
      <button className="logout-button" onClick={onLogout}>
        Logout
      </button>
    </header>
  );
};

export default Header;
