import React, { useState, useEffect } from 'react';
import { Form, Input, Button, Select, message } from 'antd';
import axios from 'axios';

const { Option } = Select;

const EditarPerfil = ({ userId }) => {
  const [form] = Form.useForm();
  const [cargos, setCargos] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    // Verifica se o ID do usuário está definido
    if (!userId) {
      message.error('ID do usuário não está definido.');
      return;
    }

    // Função para buscar os dados do usuário
    const fetchUserData = async () => {
      try {
        const response = await axios.get(
          `https://nocodb.nexusnerds.com.br/api/v2/tables/m0wcogamwt1qc5e/records?where=(Id,eq,${userId})`,
          {
            headers: {
              'xc-token': 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5',
            },
          }
        );

        if (response.status === 200 && response.data.list.length > 0) {
          const userData = response.data.list[0];
          form.setFieldsValue({
            name: userData.name,
            email: userData.email,
            password: userData.password,
            profilePicUrl: userData.profilePicUrl,
            Cargo1: userData.Cargo1,
          });
        }
      } catch (error) {
        message.error('Erro ao buscar dados do usuário.');
        console.error('Erro ao buscar dados do usuário:', error);
      }
    };

    fetchUserData();
  }, [userId, form]);

  // Função para carregar cargos disponíveis
  useEffect(() => {
    const fetchCargos = async () => {
      try {
        // Simulando um fetch dos cargos
        setCargos(['Administrador', 'Financeiro', 'Vendedor']);
      } catch (error) {
        message.error('Erro ao carregar os cargos.');
        console.error('Erro ao carregar os cargos:', error);
      }
    };

    fetchCargos();
  }, []);

  const handleSubmit = async (values) => {
    setLoading(true);
    try {
      await axios.patch(
        `https://nocodb.nexusnerds.com.br/api/v2/tables/m0wcogamwt1qc5e/records`,
        {
          Id: userId, // Inclui o ID no corpo da requisição
          name: values.name,
          email: values.email,
          password: values.password,
          profilePicUrl: values.profilePicUrl,
          Cargo1: values.Cargo1,
        },
        {
          headers: {
            'xc-token': 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5',
          },
        }
      );
      message.success('Perfil atualizado com sucesso!');
    } catch (error) {
      message.error('Erro ao atualizar o perfil.');
      console.error('Erro ao atualizar o perfil:', error);
    }
    setLoading(false);
  };

  return (
    <Form
      form={form}
      layout="vertical"
      onFinish={handleSubmit}
      initialValues={{
        Cargo1: '',
      }}
    >
      <Form.Item
        label="Nome"
        name="name"
        rules={[{ required: true, message: 'Por favor, insira seu nome!' }]}
      >
        <Input placeholder="Digite seu nome" />
      </Form.Item>
      <Form.Item
        label="Email"
        name="email"
        rules={[{ required: true, message: 'Por favor, insira seu email!' }]}
      >
        <Input placeholder="Digite seu email" />
      </Form.Item>
      <Form.Item
        label="Senha"
        name="password"
        rules={[{ required: true, message: 'Por favor, insira sua senha!' }]}
      >
        <Input.Password placeholder="Digite sua senha" />
      </Form.Item>
      <Form.Item
        label="URL da Foto de Perfil"
        name="profilePicUrl"
        rules={[{ required: true, message: 'Por favor, insira a URL da sua foto de perfil!' }]}
      >
        <Input placeholder="Digite a URL da sua foto de perfil" />
      </Form.Item>
      <Form.Item
        label="Cargo"
        name="Cargo1"
        rules={[{ required: true, message: 'Por favor, selecione seu cargo!' }]}
      >
        <Select placeholder="Selecione seu cargo">
          {cargos.map((cargo) => (
            <Option key={cargo} value={cargo}>
              {cargo}
            </Option>
          ))}
        </Select>
      </Form.Item>
      <Form.Item>
        <Button type="primary" htmlType="submit" block loading={loading}>
          Salvar Alterações
        </Button>
      </Form.Item>
    </Form>
  );
};

export default EditarPerfil;
