import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Form, Input, Button, Card, Typography, message } from 'antd';
import axios from 'axios';
import './styles/Login.css';

const { Title } = Typography;

const Login = ({ onLogin }) => {
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const handleLogin = async (values) => {
    setLoading(true);
    try {
      const { data } = await axios.get(
        `https://nocodb.nexusnerds.com.br/api/v2/tables/m0wcogamwt1qc5e/records?where=(username,eq,${values.username})`,
        {
          headers: {
            "xc-token": "ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5",
          },
        }
      );
  
      if (data && data.list.length > 0) {
        const user = data.list[0];
  
        // Verifica se a senha corresponde ao que está no banco
        if (user.password === values.password) {
          onLogin({
            id: user.Id,
            name: user.username,
            profilePic: user.profilePicUrl,
          });
          navigate("/home"); // Redireciona para a página principal após o login
        } else {
          message.error("Senha incorreta");
        }
      } else {
        message.error("Usuário não encontrado");
      }
    } catch (error) {
      console.error("Erro na autenticação:", error);
      message.error("Erro na autenticação");
    } finally {
      setLoading(false);
    }
  };
  
  
  return (
    <div className="login-container">
      <div className="login-form-wrapper">
        <Card style={{ maxWidth: 400, margin: 'auto', padding: 20 }}>
          <Title level={3} style={{ textAlign: 'center' }}>Login</Title>
          <Form layout="vertical" onFinish={handleLogin}>
            <Form.Item
              label="Usuário"
              name="username"
              rules={[{ required: true, message: 'Por favor, insira seu usuário!' }]}
            >
              <Input placeholder="Digite seu usuário" />
            </Form.Item>
            <Form.Item
              label="Senha"
              name="password"
              rules={[{ required: true, message: 'Por favor, insira sua senha!' }]}
            >
              <Input.Password placeholder="Digite sua senha" />
            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit" block loading={loading}>
                Entrar
              </Button>
            </Form.Item>
          </Form>
        </Card>
      </div>
    </div>
  );
};

export default Login;
